/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"The Other Shoe","date":"26 Sep 2002","slug":"/2002-09-26-the-other-shoe/","tags":"Y2002"},"html":"<p>A couple months ago, I mentioned lay-offs by my employer, hitting a direct co-worker of mine, and wondered if it was the beginning of a death spiral. We seem to have taken another turn for the worse. Only this time we have a <em>Betriebsrat</em> (worker's council), so instead of being told by management what's going to happen, we are being offered a choice. Either we can reduce our pay by 2-10% (sliding scale based on salary) for the next 3 months (and only the next 3 months) and give up 3-5 vacation days for this year (and only this year), and be compensated with stock options (ha!) for the financial loss, or else another round of lay-offs is inevitable. I rather doubt that enough of us our willing to sign off on the reductions, and thus my mood about work and life in general is not all that good this evening. Hmmph.</p>"},"next":{"fields":{"title":"It's Over Over There","date":"26 Sep 2002","slug":"/2002-09-26-its-over-over-there/"}},"previous":{"fields":{"title":"Back To Business","date":"27 Sep 2002","slug":"/2002-09-27-back-to-business/"}}},{"node":{"fields":{"title":"It's Over Over There","date":"26 Sep 2002","slug":"/2002-09-26-its-over-over-there/","tags":"Y2002"},"html":"<p>15 minutes until I have to wake up Christopher... You know the world has changed when I start mostly agreeing with American hawks. But the idea of moving US bases away from Germany (brought up in the <a hrf=\"http://www.papascott.de/2002/09/24/1903.php#001903\">discussion here</a> and by <a href=\"http://catotheyoungest.blogspot.com/\">Cato the Youngest</a>) on second thought doesn't sound that bad to me. Not to punish Germany, because it wouldn't be much of a punishment, and would cost the US most than it would hurt the Germans. It <a href=\"http://denbeste.nu/cd_log_entries/2002/09/EUforceprojection.shtml\">wouldn't force the Germans to increase defence spending</a>, and not would hurt the German economy in any noticable way (some communities in SW Germany would hurt, but they all voted for Stoiber anyway, so it's no skin off Schröder's nose). But it'd be a sure sign that the world has changed, and that the Americans don't need to protect anyone from the Germans anymore. OK... time to play alarm clock!</p>"},"next":{"fields":{"title":"Joschi","date":"25 Sep 2002","slug":"/2002-09-25-joschi/"}},"previous":{"fields":{"title":"The Other Shoe","date":"26 Sep 2002","slug":"/2002-09-26-the-other-shoe/"}}},{"node":{"fields":{"title":"Joschi","date":"25 Sep 2002","slug":"/2002-09-25-joschi/","tags":"Y2002"},"html":"<p>I may watch too much news together with Christopher. He's starting to recognize politicians on TV. Or at least one politician, namely Joschka Fischer. Last week ZDF was showing live speeches of the main candidates, and I was naming them for Christopher. Look, there's Edi. And there's Gerd. And Angie. And there's Joschi! Fischer does have a friendly figure on television, and his name and face have stuck with my son. At least I can give him a reason to watch the news with me instead of his Kleine Eisbär video. We might get to see Joschi!</p>\n<p>BTW, my regular readers might have missed the oodles of <a href=\"http://www.papascott.de/cgi-bin/mt-comments.cgi?entry_id=1903\">comments</a> about German-American relations after my rant about Schröder was linked to by <a href=\"http://www.denbeste.nu/\">USS Clueless</a>.</p>"},"next":{"fields":{"title":"Where Did The Time Go?","date":"24 Sep 2002","slug":"/2002-09-24-where-did-the-time-go/"}},"previous":{"fields":{"title":"It's Over Over There","date":"26 Sep 2002","slug":"/2002-09-26-its-over-over-there/"}}}],"pathPrefix":"","first":false,"last":false,"index":839,"pageCount":1101}}

/***/ })

